@import url("https://fonts.googleapis.com/css?family=Roboto:400,400i,700");
.epsilonPage{
  overflow-x: hidden;
  font-family: Roboto, sans-serif;
  background-color: seagreen;
}

/*.epsilonText h1 {
  background: rgba(0,0,0,0.5);
  padding: 50px;
}*/

.epsilonContainer{
  position: relative;
  width: 50vw;
  opacity: 0.5;
  margin: 0;
}

.epsilonVideo {
  width: 100%;
  height: 100%;
}
.epsilonText{
  position: absolute;
  color: #FFFFFF;
  bottom: 5px;
  right: 0px;
  padding-left: 5px;
  padding-right: 5px;
  background-color: rgba(0,0,0,0.5);
}
.epsilonText h1 {
  font-size: 4vw;
}
.epsilonText p {
  font-size: 2vw;
}
